.site-footer {
  border-top: 1px solid var(--color-border-subtle); 
  background-color: var(--color-surface);
  color: var(--color-text-subtle);
  padding: 1.325rem 0;   
  text-align: center;
  font-size: 0.875rem; 
  width: 100%;
}

.footer-content {
  margin: 0 auto; 
  padding: 0 3.125rem; 
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: var(--spacing-md);
  row-gap: var(--spacing-xs);
}

.footer-links a {
  display: flex; 
  transform: translateY(1px);
}

.footer-copyright {
  margin: 0; 
}

@media (max-width: 550px) {
  .site-footer {
    padding: 1rem 0;   
  }

  .footer-content {
    display: block;
  }

  .footer-links {
    column-gap: var(--spacing-sm); 
  }

  .footer-copyright {
    padding-bottom: var(--spacing-sm);
  }
}